body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.UserAppPage {
    margin: 4vmin 4vmin;
}

.companyCard.mdc-card {
    border-radius: 2vmin;
    box-shadow: 3px 5px 10px 1px rgba(130, 130, 130, .3);
    margin: 3vmin 0;
}
.companyCard.mdc-card:last-child {
    margin-bottom: 6vmin;
}
.companyCard.mdc-card:first-child {
    margin-top: 6vmin;
}

.companyCard.mdc-card .companyCardContent {
    padding: 0 3vmin;
}
.AddComPage {
    margin: 4vmin 4vmin;
}
.UserCompanyPage {
    margin: 4vmin 4vmin;
}
.UserProfilePage {
    margin: 4vmin 4vmin;
}
:root {
    --page-transition-duration: 400ms;
}

.page {
    position: absolute;
}

.page-exit {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.page-exit-active {
    opacity: 0;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    -webkit-transition: opacity 400ms, -webkit-transform 400ms;
    transition: opacity 400ms, -webkit-transform 400ms;
    transition: opacity 400ms, transform 400ms;
    transition: opacity 400ms, transform 400ms, -webkit-transform 400ms;
    -webkit-transition: opacity var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), transform var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
}

.page-enter {
    opacity: 0;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.page-enter-active {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: opacity 400ms, -webkit-transform 400ms;
    transition: opacity 400ms, -webkit-transform 400ms;
    transition: opacity 400ms, transform 400ms;
    transition: opacity 400ms, transform 400ms, -webkit-transform 400ms;
    -webkit-transition: opacity var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), transform var(--page-transition-duration);
    transition: opacity var(--page-transition-duration), transform var(--page-transition-duration), -webkit-transform var(--page-transition-duration);
}


